<template>
  <div>
    <div id="header" class="content">
      <header-nav></header-nav>
    </div>
    <div class="content padding-top-100">

      <el-form ref="form" :model="form" label-width="80px">
        <div class="tu">
          <el-form-item label="成品图">
            <el-upload
              action="#"
              :http-request="handleHttpRequest"
              :show-file-list="false"
              :on-remove="handleRemove"
              class="avatar-uploader">
              <img v-if="form.cpimg!=null" :src="osshost+form.cpimg" class="avatar">
              <i v-else class="el-icon-plus step-upicon avatar">点击上传</i>
            </el-upload>
            <el-tag type="info">只能上传jpg/png文件，且不超过5mb</el-tag>
          </el-form-item>

        </div>
        <el-form-item label="菜谱名称">
          <el-input v-model="form.title" style="width:500px"></el-input>
        </el-form-item>
        <el-form-item label="食谱简介">
          <el-input type="textarea" v-model="form.story" style="width:500px"></el-input>
        </el-form-item>
        <el-form-item label="烹饪难度">
          <el-select v-model="form.difficult" placeholder="请选择难度">
            <el-option
              v-for="(item,index) in hardSort"
              :key="index"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="耗时">
          <el-select v-model="form.spendtime" placeholder="请选择耗时">
            <el-option
              v-for="(item,index)  in timeSort"
              :key="index"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="食材清单">
          <el-form-item class="ml10">
            <el-col :span="8">
              食材
            </el-col>
            <el-col class="line" :span="2"></el-col>
            <el-col :span="8" class="ml30">
              用量
            </el-col>
          </el-form-item>
          <el-form-item
            class="mb10"
            v-for="(item,stuffIndex) in stuffList"
            :key="stuffIndex"
          >
            <el-col :span="8">
              <el-input placeholder="食材：如五花肉" v-model="item.name"></el-input>
            </el-col>
            <el-col :span="8" class="ml30">
              <el-input placeholder="用量：如500g" v-model="item.num"></el-input>
            </el-col>
            <el-col :span="4" class="ml10">
              <el-button type="danger" icon="el-icon-delete" circle plain
                         @click.prevent="delFood(stuffIndex)"></el-button>
            </el-col>
          </el-form-item>
          <el-button @click="addFood()">添加1个食材</el-button>
        </el-form-item>
        <el-form-item label="制作步骤">
          <el-tag type="info">只能上传jpg/png文件，且不超过5mb</el-tag>
        </el-form-item>

        <el-form-item
          class="mb10"
          v-for="(item,index) in stepList"
          :key="index" id="step_upload"
        >
          <el-col :span="2">
            <el-tag type="info">第{{index+1}}步</el-tag>
          </el-col>
          <el-col :span="9">
            <el-upload
              action="#"
              :data="{a:index}"
              :http-request="stepHttpRequest"
              :show-file-list="false"
            >
              <img v-if="item.stepImg!=null" :src="osshost+item.stepImg" class="avatar">
              <i v-else class="el-icon-plus step-upicon avatar">点击上传</i>
            </el-upload>
            <el-input type="textarea" maxlength="120" show-word-limit v-model="item.description"
                      style="width:350px"></el-input>
          </el-col>
          <el-col :span="4" class="ml10">
            <el-button type="danger" icon="el-icon-delete" circle plain @click.prevent="delStep(index)"></el-button>
          </el-col>
        </el-form-item>
        <el-form-item>
          <el-button @click="addstep()">添加1个步骤</el-button>
        </el-form-item>
        <el-form-item label="小贴士">
          <el-tag type="info">（选填，关于这道菜的注意事项、小秘诀，不超过300字）</el-tag>
          <el-input type="textarea" v-model="form.tip"></el-input>
        </el-form-item>
        <el-form-item>
          <el-checkbox-group v-model="form.ispushweb">
            <el-checkbox label="独家菜谱【只在管饭网发布】"></el-checkbox>
          </el-checkbox-group>
          <el-checkbox-group v-model="form.ispushdt">
            <el-checkbox label="生成动态"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item>
          <el-button v-if="type!=3" type="2" @click="uploadFood(2)">发布食谱</el-button>
          <el-button v-if="type==3" type="1" @click="uploadeFood(2)">确认修改食谱</el-button>
          <!-- <el-button v-if="type!=3">存草稿</el-button> -->
        </el-form-item>
      </el-form>

    </div>
    <FooterBottom></FooterBottom>
  </div>
</template>

<script>
  import headerNav from "@/components/headerNav";
  import FoodItem from "@/components/foodItem";
  import FooterBottom from "@/components/footer";
  import {Message} from "element-ui";
  import {uploadImg} from "../api/operate";
  import {pushMenu, getMenuEdit, pushMenuEdit} from "../api/menu"
  import tools from '@/utils/tools'

  let OSS = require('ali-oss');
  export default {
    components: {
      headerNav,
      FoodItem,
      FooterBottom
    },
    data() {
      return {
        osshost: process.env.UPLOAD_URL,
        stepIndex: 0,
        type: '',
        campaignid:'',
        keword:'',
        form: {
          title: '',
          cpimg: null,
          difficult: '',
          spendtime: '',
          tip: '',
          stuff: '',
          step: '',
          ispushweb: true,
          ispushdt: true,
          campaignid:'',
        },
        stuffList: [{
          num: '',
          name: ''
        }],
        stepList: [{
          stepImg: null,
          description: '',
        }],
        hardSort: ['入门', '中级', '高级'],
        timeSort: ['10分钟以下', '10~30分钟', '30~60分钟', '1小时以上']
      }
    },
    mounted() {
    },
    created() {
      this.cpid = this.$route.query.id;
      this.campaignid = this.$route.query.aid;
      this.keword = this.$route.query.keword;
      this.type = this.$route.query.type;
      if(this.keword){
        this.form.title='#'+this.keword+'#';
      }
      if (this.type == 3) {
        this.getDataInfo(this.cpid);
      }

    },
    methods: {
      getDataInfo(cpid) {
        getMenuEdit(cpid).then(response => {
          let res = response.data;
          if (res.success) {
            this.form = res.result;
            this.stepList = this.form.step;
            this.stuffList = this.form.stuff.split('|');
            for (var j = 0; j < this.stuffList.length; j++) {
              let array = [];
              array = this.stuffList[j].split('#');
              this.stuffList[j] = {
                name: array[0],
                num: array[1]
              }
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      handleHttpRequest(a) {//上传封面图
        let dialogImageUrl = a.file.name;
        let type = 1;
        uploadImg(type).then(response => {
          let result = response.data.result;
          let imgUrl;
          imgUrl = result.spaceid + '/' + tools.imgMd5(dialogImageUrl, imgUrl, 'caipu');//文件名 加密
          const client = new OSS({
            region: 'oss-cn-hangzhou',
            accessKeyId: result.AccessKeyId,
            accessKeySecret: result.AccessKeySecret,
            stsToken: result.SecurityToken,
            bucket: 'guanfan'//请求阿里云之后获得
          })
          client.put(imgUrl, a.file).then(results => {
            this.form.cpimg = results.name;
            // if(results.res.requestUrls[0].indexOf('?') != -1){
            //   this.url = results.res.requestUrls[0].split('?')[0];
            // }else{
            //   //console.log('图片100k以内')
            //   this.url = results.res.requestUrls[0]
            // }
            console.log('头像上传成功');
          }).catch((err) => {
            console.log(err);
          });
        }).catch(error => {
          console.log(error)
        })
      },
      handleRemove() {
        this.form.cpimg = null;
      },
      delFood(index) {//删除食材
        if (index !== -1) {
          this.stuffList.splice(index, 1)
        }
      },
      addFood() {//增加食材
        let object = {
          name: '',
          num: ''
        };
        this.stuffList.push(object);
      },
      delStep(index) {//删除步骤
        this.stepList.splice(index, 1);
      },
      addstep() {//增加步骤
        let object = {
          stepImg: null,
          description: ''
        };
        this.stepList.push(object);
      },
      stepHttpRequest(a) {//上传步骤图
        let dialogImageUrl = a.file.name;
        let index = a.data.a;
        let type = 1;
        uploadImg(type).then(response => {
          let res = response.data;
          if (res.success) {
            let result = res.result;
            let imgUrl;
            imgUrl = result.spaceid + '/' + tools.imgMd5(dialogImageUrl, imgUrl, 'caipu');//文件名 加密
            const client = new OSS({
              region: 'oss-cn-hangzhou',
              accessKeyId: result.AccessKeyId,
              accessKeySecret: result.AccessKeySecret,
              stsToken: result.SecurityToken,
              bucket: 'guanfan'//请求阿里云之后获得
            })
            client.put(imgUrl, a.file).then(results => {
              this.stepList[index].stepImg = results.name;
              console.log(this.stepList[index].stepImg);
              console.log('头像上传成功');
            }).catch((err) => {
              console.log(err);
            });
          }
        }).then(error => {
          console.log(error)
        })
      },
      uploadFood(type) {
        this.form.type = type;
          this.form.stuff = subList(this.form.stuff, this.stuffList, 1)
          this.form.step = subList(this.form.step, this.stepList, 2)
        console.log(this.form.step);
        if(this.campaignid!=0){
          this.form.campaignid=this.campaignid;
        }

        function subList(string, list, type) { //type 1 食材   2 步骤
          if (type == 1) {
            for (var i = 0; i < list.length; i++) {  //拼接食材
              if (i == 0) {
                string += list[i].name + '#' + list[i].num;
              } else {
                string += '|' + list[i].name + '#' + list[i].num;
              }
            }
          } else {
            for (var i = 0; i < list.length; i++) {  //拼接步骤
              if (i == 0) {
                string += (i + 1) + '#' + list[i].stepImg + '#' + list[i].description;
              } else {
                string += '|' + (i + 1) + '#' + list[i].stepImg + '#' + list[i].description;
              }
            }
          }
          return string;
        }
        if(this.form.ispushweb==true){
          this.form.ispushweb=1;
        }else{
          this.form.ispushweb=0;
        }
        if(this.form.ispushdt==true){
          this.form.ispushdt=1;
        }else{
          this.form.ispushdt=0;
        }
        pushMenu(this.form).then(response => {
          let res = response.data;
          if (res.success) {
            Message({
              message: '发布成功！',
              type: 'success',
              duration: 5 * 1000
            })
            this.$router.go(-1);//返回上一层
          } else {
            Message({
              message: '发布失败！',
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
          this.form.stuff=this.form.stuff;
          this.form.step=this.form.step;
        })
      },
      uploadeFood(type) {
        this.form.type = type;
        this.form.stuff = subList(this.form.stuff, this.stuffList, 1)
        this.form.step = subList(this.form.step, this.stepList, 2)

        function subList(string, list, type) { //type 1 食材   2 步骤
          if (type == 1) {
            for (var i = 0; i < list.length; i++) {  //拼接食材
              if (i == 0) {
                string += list[i].name + '#' + list[i].num;
              } else {
                string += '|' + list[i].name + '#' + list[i].num;
              }
            }
          } else {
            for (var i = 0; i < list.length; i++) {  //拼接步骤
              if (i == 0) {
                string += (i + 1) + '#' + list[i].url + '#' + list[i].description;
              } else {
                string += '|' + (i + 1) + '#' + list[i].url + '#' + list[i].description;
              }
            }
          }
          return string;
        }
        if(this.form.ispushweb==true){
          this.form.ispushweb=1;
        }else{
          this.form.ispushweb=0;
        }
        if(this.form.ispushdt==true){
          this.form.ispushdt=1;
        }else{
          this.form.ispushdt=0;
        }
        pushMenuEdit(this.form).then(response => {
          let res = response.data;
          if (res.success) {
            Message({
              message: '修改成功！',
              type: 'success',
              duration: 5 * 1000
            })
          } else {
            Message({
              message: '修改失败！',
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      }
    }
  };
</script>

<style scoped>
  .tu {
    width: 750px;
    height: 230px;
    margin: 40px 0 20px;
  }
  .step-upicon {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    font-size: 14px;
    color: #8c939d;
    width: 150px;
    height: 150px;
    line-height: 200px;
    text-align: center;
  }

  .step-upicon:hover {
    border-color: #409EFF;
  }

  .avatar-uploader .avatar-uploader-icon {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .avatar-uploader-icon:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 500px;
    height: 200px;
    line-height: 200px;
    text-align: center;
  }

  .avatar {
    width: 350px;
    height: 200px;
    display: block;
  }
</style>



